.live-preview-sticky {
    /* background-color: pink; */
    position: -webkit-sticky;
    position: sticky;
    top: 70px;
    overflow: scroll;
    overflow-x: hidden;
    /* overflow-y: scroll; */
    /* height: 560px; */
    height: 86vh;
}

.markdown-parent {
    white-space: pre-line;
    /* color: #3E3E3E; */
}

.markdown-parent img {
    width: 70%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.markdown-parent p, .markdown-parent li  {
    
    font-family: "Roboto";
    font-size: 20px;
    line-height: 40px;
    
}

/* .markdown-parent code {
 
} */

.markdown-parent pre {
    white-space: pre-wrap;
    background-color: #f6f8fa;
    padding: 20px;

    border-radius: 5px;
    border: 1px solid #a71e5e;

    /* width: 100%; */
}

.markdown-parent code {
    background-color: #f6f8fa;
}

table {
    padding: 0;
}
table tr {
    border-top: 1px solid #cccccc;
    background-color: white;
    margin: 0;
    padding: 0;
}
table tr:nth-child(2n) {
    background-color: #f8f8f8;
}
table tr th {
    font-weight: bold;
    border: 1px solid #cccccc;
    text-align: left;
    margin: 0;
    padding: 6px 13px;
}
table tr td {
    border: 1px solid #cccccc;
    text-align: left;
    margin: 0;
    padding: 6px 13px;
}
table tr th :first-child,
table tr td :first-child {
    margin-top: 0;
}
table tr th :last-child,
table tr td :last-child {
    margin-bottom: 0;
}

dl {
    padding: 0;
}
dl dt {
    font-size: 14px;
    font-weight: bold;
    font-style: italic;
    padding: 0;
    margin: 15px 0 5px;
}
dl dt:first-child {
    padding: 0;
}
dl dt > :first-child {
    margin-top: 0;
}
dl dt > :last-child {
    margin-bottom: 0;
}
dl dd {
    margin: 0 0 15px;
    padding: 0 15px;
}
dl dd > :first-child {
    margin-top: 0;
}
dl dd > :last-child {
    margin-bottom: 0;
}

/* this is for image upload preview */
.upload-preview-image-parent {
    position: relative;
}

.upload-preview-image {
    width: 50vw;
    height: auto;
    max-width: 200px;
    max-height: auto;
}

.upload-preview-cancel {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 200;
    color: rgb(230, 230, 230);
}

.upload-preview-cancel:hover {
    cursor: pointer;
}

@media (max-width: 600px) {
    .upload-preview-image {
        width: 100%;
        height: auto;
        max-width: 500px;
        max-height: auto;
    }

    .markdown-parent img {
        width: 100%;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
}

