.about-hero {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  background-image: url(/static/media/bg.414567c6.png);
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
  display: grid;
  align-items: center;
}

.about-hero .about-parent {
  width: 100%;
  max-width: 600px;
  position: absolute;
  right: 0;
  margin: 60px;
}

.about-hero .about-parent .about-divider {
  margin: 10px 0 50px;
  width: 150px;
}

@media (max-width: 700px) {
  .about-hero .about-parent .about-divider {
    width: 70px;
  }
}

@media (max-width: 700px) {
  .about-hero .about-parent {
    position: relative;
    left: 0;
    padding: 20px;
    margin: 0px;
    box-sizing: border-box;
  }
}

@media (max-width: 700px) {
  .about-hero {
    display: block;
    margin: 0px;
    height: 100%;
  }
}
