    width: 30%;
  }
}

@media (max-width: 650px) {
  .vertical-nav-hero .avatar-parent img {
    width: 40%;
  }
}

.vertical-nav-hero .divider {
  margin: 30px;
  width: 150px;
}

@media (max-width: 960px) {
  .vertical-nav-hero .divider {
    display: none;
  }
}

.vertical-nav-hero .title {
  padding: 0 30px 30px;
  text-align: center;
  box-sizing: border-box;
}

.vertical-nav-hero .nav {
  margin-top: 60px;
  padding: 30px;
  box-sizing: border-box;
}

.vertical-nav-hero .nav :hover {
  color: #04bcff;
  cursor: pointer;
}

@media (max-width: 960px) {
  .vertical-nav-hero .nav {
    text-align: center;
  }
}
